import axios from 'axios';
import React from 'react';
//Import Movie Card and Movie View components
import { RegistrationView } from '../registration-view/registration-view';
import { MovieCard } from '../movie-card/movie-card';
import { MovieView } from '../movie-view/movie-view';
import { LoginView } from '../login-view/login-view';

export class MainView extends React.Component {
    constructor() {
        // Method to create component
        super(); // Calls constructor of the parent class
        this.state = {
            movies: [],
        };
    }

    componentDidMount() {
        axios
            .get('https://enigmatic-hamlet-36885.herokuapp.com/movies')
            .then((response) => {
                this.setState({
                    movies: response.data,
                });
            })
            .catch((error) => {
                console.log(error);
            });
    }

    // Event listenter function
    setSelectedMovie(newSelectedMovie) {
        this.setState({
            selectedMovie: newSelectedMovie,
        });
    }

    onLogggedIn(user) {
        this.setState({
            user,
        });
    }
    render() {
        //Desctructured object which contains movies list
        const { movies, selectedMovie, user, registration } = this.state;

        if (!registration)
            return (
                <RegistrationView
                    onRegistration={(registration) =>
                        this.onRegistration(registration)
                    }
                />
            );

        if (!user)
            return <LoginView onLoggedIn={(user) => this.onLoggedIn(user)} />;

        // If movies list equals 0 "The list is empty" will render in the DOM
        if (movies.length === 0) return <div className="main-view" />;

        return (
            // Displays details of each movie
            //Returns to Main View when back button is clicked
            <div className="main-view">
                {selectedMovie ? (
                    <MovieView
                        movieDetails={selectedMovie}
                        onBackClick={(newSelectedMovie) => {
                            this.setSelectedMovie(newSelectedMovie);
                        }}
                    />
                ) : (
                    // Iterates over movie object and returns unique ID and movie data
                    //Event listener that listens to click and is directed to Movie View
                    movies.map((movie) => (
                        <MovieCard
                            key={movie._id}
                            movieData={movie}
                            onMovieClick={(movie) => {
                                this.setSelectedMovie(movie);
                            }}
                        />
                    ))
                )}
            </div>
        );
    }
}

export default MainView;
